import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

class Header extends Component {
  render(){
    return(
      <div className="position-fixed nav-class  d-flex align-items-end"  data-aos="fade-down" data-aos-duration="1200" data-aos-delay="800">
        <div className="col-12 p-0">
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container">
          <div className="col-9 p-0 float-left">
        <nav className="navbar-brand fw-8">COVID-19 Dashboard</nav>
        </div>
        <div className="col-3 p-0 float-right text-right">
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
          <div className="navbar-nav mar-auto">
          <NavLink className="nav-item nav-link fw-7" data-aos="fade-down" data-aos-duration="1200" data-aos-delay="1200" exact to="/covid19/">Home</NavLink>
            <NavLink className="nav-item nav-link fw-7" data-aos="fade-down" data-aos-duration="1200" data-aos-delay="1500" exact to="/covid19/local">Local</NavLink>
            <NavLink className="nav-item nav-link fw-7" data-aos="fade-down" data-aos-duration="1200" data-aos-delay="1800" exact to="/covid19/global">Global</NavLink>
          </div>
        </div>
        </div>
        </div>
      </nav>
      </div>
      </div>
      )
      }
  }


export default Header