import React  from 'react'

import DashboardCard from './DashBoardCard'

const LocalDashboard = ({ dashboardData }) => {
        return(
          <div className="container mtz">
            <div className="my-3">
              <div className="main-heading p-3" data-aos="fade-up" data-aos-duration="1200">
                 <h2>Local Information</h2>
                 <p>Last Updated: {dashboardData.update_date_time}</p>
              </div>
              <div className="container">
              <div className="row">
                <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#fff4c2'}}>
                  <DashboardCard 
                      cardTitle="New Cases"
                      count={dashboardData.local_new_cases}
                      
                    />
                  </div>
                </div>
                <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#9ed5ff'}}>
                    <DashboardCard 
                      cardTitle="Total Cases"
                      count={dashboardData.local_total_cases}

                    />
                </div>
                </div>
                <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#ffe988'}}>
                    <DashboardCard 
                      cardTitle="Active"
                      count={dashboardData.local_active_cases}
                    />
                    </div>
                    </div>
                    <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#ffd4d4'}}>
                    <DashboardCard 
                      cardTitle="New Deaths"
                      count={dashboardData.local_new_deaths}
                    />
                </div>
                </div>
                    <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#ffd4d4'}}>
                    <DashboardCard 
                      cardTitle="Total Deaths"
                      count={dashboardData.local_deaths}
                    />
                </div>
                </div>
                <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#c4e4fc'}}>
                    <DashboardCard 
                      cardTitle="Admitted"
                      count={dashboardData.local_total_number_of_individuals_in_hospitals}

                    />
                </div>
                </div>
                <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                  <div className="card my-3" style={{backgroundColor: '#c4f8bd'}}>
                    <DashboardCard 
                      cardTitle="Recovered"
                      count={dashboardData.local_recovered}

                    />
                </div>
                </div>
                </div>
                </div>
                </div>
              </div>
        )
    }

export default LocalDashboard 