import React, { useState, useEffect } from 'react'
import { Route, Switch } from 'react-router-dom'
import LocalDashBoard from './LocalDashboard'
import Home from './Home'
import GlobalDashboard from './GlobalDashboard'



function Dashboard (){
const [stats, setStats] = useState({ dashboard: {}, hospitalData: [] });

useEffect(() => {
fetch('https://hpb.health.gov.lk/api/get-current-statistical')
.then(response => response.json())
.then(response => {
if (response.success === true) {
setStats({
dashboard: response.data,
hospitalData: response.data.hospital_data
});
}
})
.catch(() => console.log('Something went wrong...'));
}, []);


const Dashboard = () => {
return(
<React.Fragment>
    <div className="container mtz">
        <Home />
    </div>
</React.Fragment>
)
}
return(
<React.Fragment>
        <Switch>
            <Route exact path="/covid19/">
                {stats.hospitalData.length > 0 ? (
                Dashboard()
                ): (
                    <div className="container text-center">
                <div className="spinner-grow" style={{marginTop: 150, width: 250, height: 250}} role="status">
                <span className="sr-only">Fetching Data...</span>
              </div>
                    </div>
                )}
            </Route>
            <Route exact path='/covid19/local'>
            <LocalDashBoard dashboardData={stats.dashboard} />
            <div className="col-md-12 text-center">
                    <p className="note w-100 fw-6" data-aos="fade-up" data-aos-duration="2000" data-aos-delay="300">This might take several minutes to be updated, since Health Promotion Bureau is issuing verified data from reliable sources.</p>
                </div>
            </Route>
            <Route exact path='/covid19/global'>
            <GlobalDashboard dashboardData={stats.dashboard} />
            <div className="col-md-12 text-center">
                    <p className="note  w-100 fw-6" data-aos="fade-up" data-aos-duration="2000" data-aos-delay="300">This might take several minutes to be updated, since Health Promotion Bureau is issuing verified data from reliable sources.</p>
                </div>
            </Route>
        </Switch>
</React.Fragment>
)
}

export default Dashboard;