import React from 'react'

import DashBoardCard from './DashBoardCard';

const GlobalDashboard = ({dashboardData}) => {
    return(
        <div className="container mtz">
        <div className="my-3">
            <div className="main-heading p-3" data-aos="fade-up" data-aos-duration="1200">
            <h2 className="">Global Information</h2>
            <p>Last Updated: {dashboardData.update_date_time}</p>
            </div>
            <div className="container">
            <div className="row">
            <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                <div className="card my-3" style={{backgroundColor: '#fff4c2'}}>
                <DashBoardCard 
                    cardTitle="New Cases"
                    count={dashboardData.global_new_cases}
                    countUp={dashboardData.global_new_cases}
                />
             </div></div>
            <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                <div className="card my-3" style={{backgroundColor: '#9ed5ff'}}>

                <DashBoardCard 
                    cardTitle="Total Cases"
                    count={dashboardData.global_total_cases}

                /></div>
        </div>

            <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                <div className="card my-3" style={{backgroundColor: '#ea7d7e'}}>

                <DashBoardCard 
                    cardTitle="Total Deaths"
                    count={dashboardData.global_deaths}

                /></div>
        </div>
            <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                <div className="card my-3" style={{backgroundColor: '#ffbcbc'}}>

                <DashBoardCard 
                    cardTitle="New Deaths"
                    count={dashboardData.global_new_deaths}

                /></div>
        </div>

            <div className="col-md-3 col-sm-4 col-6 np-5 np" data-aos="fade-up" data-aos-duration="1200">
                <div className="card my-3" style={{backgroundColor: '#c4f8bd'}}>

                <DashBoardCard 
                    cardTitle="Recovered"
                    count={dashboardData.global_recovered}

                /></div>
        </div>
        </div>
        </div>
            </div>
           
        </div>
    )
}


export default GlobalDashboard