import React from 'react'

function DashBoardCard(props){
    return(
        <React.Fragment>
                <p className="card-title p-3 text-center fw-6" style={{backgroundColor: "white"}}>{props.cardTitle}</p>
                <div className="card-body">
                    <p className="text-dark bg-transparent rounded-circle text-center align-middle py-5 fw-7">{props.count}</p>
                </div>
        </React.Fragment>
    )
}

export default DashBoardCard